(function () {
    angular.module('OfficeHub').service('MessagesService', ['$http', '$q', function ($http, $q) {
      //#region Initialization
      //----------------------------------------------------------------------
      // Gets a reference to self
      //----------------------------------------------------------------------
      var self = this;
      //#endregion

      //#region Private Properties
      //#endregion
      
      //#region Public Methods
      this.getMessages = function (pageSize, pageNumber) {
        //----------------------------------------------------------------------
        // Use a promise to resolve the data from Office 365
        //----------------------------------------------------------------------
        var deferred = $q.defer();
        
            //----------------------------------------------------------------------
        // Build the REST Endpoint URL
        //----------------------------------------------------------------------
        var uri = "https://graph.microsoft.com/v1.0/me/MailFolders/Inbox/messages?&top=" + pageSize;

        //----------------------------------------------------------------------
        // Builds the query to skip the elements of the past page
        //----------------------------------------------------------------------
        if (pageNumber > 1)
        {
          uri += "&$skip=" + ((pageNumber - 1) * pageSize);
        }

        //----------------------------------------------------------------------
        // Executes the query on Office 365
        //----------------------------------------------------------------------
        $http.get(uri).success(function (data, status, headers, config) {
          //----------------------------------------------------------------------
          // If the request is successful, resolve the returned data
          //----------------------------------------------------------------------
          deferred.resolve(data);
        }).error(function (data, status, headers, config) {
          //----------------------------------------------------------------------
          // If the request is unsuccessful, reject the promise
          //----------------------------------------------------------------------
          deferred.reject();
        });

        //----------------------------------------------------------------------
        // Returns the async promise
        //----------------------------------------------------------------------
        return deferred.promise;
      };

      this.getMessage = function (messageId) {
        //----------------------------------------------------------------------
        // Use a promise to resolve the data from Office 365
        //----------------------------------------------------------------------
        var deferred = $q.defer();

        //----------------------------------------------------------------------
        // Build the REST Endpoint URL
        //----------------------------------------------------------------------
        var uri = "https://graph.microsoft.com/v1.0/me/messages/" + messageId;

        //----------------------------------------------------------------------
        // Executes the query on Office 365
        //----------------------------------------------------------------------
        $http.get(uri).success(function (data, status, headers, config) {
          //----------------------------------------------------------------------
          // If the request is successful, resolve the returned data
          //----------------------------------------------------------------------
          deferred.resolve(data);
        }).error(function (data, status, headers, config) {
          //----------------------------------------------------------------------
          // If the request is unsuccessful, reject the promise
          //----------------------------------------------------------------------
          deferred.reject();
        });

        //----------------------------------------------------------------------
        // Returns the async promise
        //----------------------------------------------------------------------
        return deferred.promise;
      };

      this.updateMessage = function (messageId, data) {
        //----------------------------------------------------------------------
        // Use a promise to resolve the data from Office 365
        //----------------------------------------------------------------------
        var deferred = $q.defer();

        //----------------------------------------------------------------------
        // Build the REST Endpoint URL
        //----------------------------------------------------------------------
        var uri = "https://graph.microsoft.com/v1.0/me/messages/" + messageId;

        //----------------------------------------------------------------------
        // Executes the query on Office 365
        //----------------------------------------------------------------------
        $http.patch(uri, data).success(function (data, status, headers, config) {
          //----------------------------------------------------------------------
          // If the request is successful, resolve the returned data
          //----------------------------------------------------------------------
          deferred.resolve(data);
        }).error(function (data, status, headers, config) {
          //----------------------------------------------------------------------
          // If the request is unsuccessful, reject the promise
          //----------------------------------------------------------------------
          deferred.reject();
        });

        //----------------------------------------------------------------------
        // Returns the async promise
        //----------------------------------------------------------------------
        return deferred.promise;
      };

      this.deleteMessage = function (messageId) {
        //----------------------------------------------------------------------
        // Use a promise to resolve the data from Office 365
        //----------------------------------------------------------------------
        var deferred = $q.defer();

        //----------------------------------------------------------------------
        // Build the REST Endpoint URL
        //----------------------------------------------------------------------
        var uri = "https://graph.microsoft.com/v1.0/me/messages/" + messageId;

        //----------------------------------------------------------------------
        // Executes the query on Office 365
        //----------------------------------------------------------------------
      $http.delete(uri).success(function (data, status, headers, config) {
        //----------------------------------------------------------------------
        // If the request is successful, resolve the returned data
        //----------------------------------------------------------------------
        deferred.resolve(data);
      }).error(function (data, status, headers, config) {
        //----------------------------------------------------------------------
        // If the request is unsuccessful, reject the promise
        //----------------------------------------------------------------------
        deferred.reject();
      });

      //----------------------------------------------------------------------
      // Returns the async promise
      //----------------------------------------------------------------------
      return deferred.promise;
    };

    this.sendMessage = function (recipients, subject, body, saveToSentItems) {
      //----------------------------------------------------------------------
      // Use a promise to resolve the data from Office 365
      //----------------------------------------------------------------------
      var deferred = $q.defer();

      //----------------------------------------------------------------------
      // Build the REST Endpoint URL
      //----------------------------------------------------------------------
      var uri = "https://graph.microsoft.com/v1.0/me/microsoft.graph.sendMail";

      //----------------------------------------------------------------------
      // Builds the necessary data object to send to the send an email
      //----------------------------------------------------------------------
      var data = {
        Message: {
          Subject: subject,
          Body: {
            ContentType: "HTML",
            Content: body
          },
          ToRecipients: []
        },
        SaveToSentItems: saveToSentItems
      }

      //----------------------------------------------------------------------
      // Adds the recipients to the message data
      //----------------------------------------------------------------------
      angular.forEach(recipients, function (recipient) {
        data.Message.ToRecipients.push({
          EmailAddress: {
            Address: recipient.trim()
          }
        });
      });

      //----------------------------------------------------------------------
      // Executes the query on Office 365
      //----------------------------------------------------------------------
      $http.post(uri, data).success(function (data, status, headers, config) {
        //----------------------------------------------------------------------
        // If the request is successful, resolve the returned data
        //----------------------------------------------------------------------
        deferred.resolve(data);
      }).error(function (data, status, headers, config) {
        //----------------------------------------------------------------------
        // If the request is unsuccessful, reject the promise
        //----------------------------------------------------------------------
        deferred.reject();
      });

      //----------------------------------------------------------------------
      // Returns the async promise
      //----------------------------------------------------------------------
      return deferred.promise;
    };

    this.createReply = function (messageId) {
      //----------------------------------------------------------------------
      // Use a promise to resolve the data from Office 365
      //----------------------------------------------------------------------
      var deferred = $q.defer();

      //----------------------------------------------------------------------
      // Build the REST Endpoint URL
      //----------------------------------------------------------------------
      var uri = "https://graph.microsoft.com/v1.0/me/messages/" + messageId + "/CreateReply";

      //----------------------------------------------------------------------
      // Executes the query on Office 365
      //----------------------------------------------------------------------
      $http.post(uri).success(function (data, status, headers, config) {
        //----------------------------------------------------------------------
        // If the request is successful, resolve the returned data
        //----------------------------------------------------------------------
        deferred.resolve(data);
      }).error(function (data, status, headers, config) {
        //----------------------------------------------------------------------
        // If the request is unsuccessful, reject the promise
        //----------------------------------------------------------------------
        deferred.reject();
      });

      //----------------------------------------------------------------------
      // Returns the async promise
      //----------------------------------------------------------------------
      return deferred.promise;
    };
    //#endregion
  }]);
}());
(function () {
    angular.module('OfficeHub').controller('ReplyController', ['$state', '$stateParams', 'MessagesService', function ($state, $stateParams, MessagesService) {
        //#region Initialization
        //----------------------------------------------------------------------
        // Gets a reference to self
        //----------------------------------------------------------------------
        var vm = this;
        //#endregion

        //#region Private Members
        var messageId = null;
        //#endregion

        //#region Public Members
        //----------------------------------------------------------------------
        // The original message that is used
        //----------------------------------------------------------------------
        vm.Message = null;
        
        //----------------------------------------------------------------------
        // The adresses to send the email to
        //----------------------------------------------------------------------
        vm.ToAddresses = "";
        
        //----------------------------------------------------------------------
        // The adresses to cc the email to
        //----------------------------------------------------------------------
        vm.CcAddresses = "";

        //----------------------------------------------------------------------
        // The subject of the email
        //----------------------------------------------------------------------
        vm.Subject = "";

        //----------------------------------------------------------------------
        // The body the email
        //----------------------------------------------------------------------
        vm.Body = "";
        //#endregion

        //#region Public Methods    
        vm.sendMessage = function () {
          //----------------------------------------------------------------------
          // Sends the message to the recipients
          //----------------------------------------------------------------------
          MessagesService.sendMessage(vm.ToAddresses.split(';'), vm.Subject, vm.Body, true).then(function (data) {
            //----------------------------------------------------------------------
            // Redirects to the inbox
            //----------------------------------------------------------------------
            $state.go('root.messages');
          }, function () {
            //----------------------------------------------------------------------
            // If an error occured, do nothing
            //----------------------------------------------------------------------
          });
        };
             
        vm.cancel = function() {
          if(vm.Message) {
            $state.go('root.message', { id: vm.Message.Id });
          } else {
            $state.go('root.messages');
          }
          
        }
        //#endregion

        //#region Private Methods
        var init = function () {
          messageId = $stateParams.id;
          
          loadMessage().then(function () {
            //----------------------------------------------------------------------
            // Loads first page of the messages
            //----------------------------------------------------------------------
            createReply();
          });
        };
        
       var loadMessage = function() {
          //----------------------------------------------------------------------
          // Loads the message based on the current messageId
          //----------------------------------------------------------------------
          return MessagesService.getMessage(messageId).then(function (message) {
            //----------------------------------------------------------------------
            // Update the view data
            //----------------------------------------------------------------------
            vm.Message = message;
            
            if (!vm.Message.isRead) {
              //----------------------------------------------------------------------
              // If the email is not read, flag it as read
              //----------------------------------------------------------------------
              vm.toggleReadStatus();
            }
          }, function () {
              vm.Messages = null;
          });
        };
        
        var createReply = function () {
          //----------------------------------------------------------------------
          // Creates a reply of the requested message
          //----------------------------------------------------------------------
          return MessagesService.createReply(messageId).then(function (data) {
            //----------------------------------------------------------------------
            // Assign the view data to the reply received (title with RE:, etc.)
            //----------------------------------------------------------------------
            vm.ToAddresses = vm.Message.sender.emailAddress.address;
            vm.Subject = data.subject;
            vm.Body = "<br /><br />" + data.body.content;
          }, function () {
            //----------------------------------------------------------------------
            // If an error occured, do nothing
            //----------------------------------------------------------------------
          });
        };
        //#endregion

        //#region Initialization
        init();
        //#endregion
    }]);
}());
