(function() {
  'use strict';

  angular
    .module('OfficeHub')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $state) {
    $rootScope.$state = $state;
    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('OfficeHub')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {   
    //$locationProvider.html5Mode(true);
     
    $stateProvider
      .state('root', {
        url: '',
        abstract: true,
        views: {
          'header': {
            templateUrl: 'app/components/header/header.html'
          },
          'footer': {
            templateUrl: 'app/components/footer/footer.html'
          },
          'navigation': {
            templateUrl: 'app/components/navigation/navigation.html'
          }
        }
      })
      .state('root.home', {
        url: '/',
        views: {
           'container@': { 
            templateUrl: 'app/components/home/home.html'
          }
        }
      })
      .state('root.messages', {
        url: '/messages',
        views: {
          'container@': {
            templateUrl: 'app/components/messages/views/inbox.html',
            controller: "InboxController",
            controllerAs: "vm"
          }
        },
        requireADLogin: true
      })
      .state('root.messageNew', {
        url: '/messages/compose',
        views: {
          'container@': {
            templateUrl: 'app/components/messages/views/compose.html',
            controller: "NewMessageController",
            controllerAs: "vm"
          }
        },
        requireADLogin: true
      })
      .state('root.message', {
        url: '/messages/:id',
        views: {
          'container@': {
            templateUrl: 'app/components/messages/views/message.html',
            controller: "MessageController",
            controllerAs: "vm"
          }
        },
        requireADLogin: true
      })
      .state('root.messageReply', {
        url: '/messages/compose/:id',
        views: {
          'container@': {
            templateUrl: 'app/components/messages/views/compose.html',
            controller: "ReplyController",
            controllerAs: "vm"
          }
        },
        requireADLogin: true
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('OfficeHub')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('OfficeHub')
    .config(config);

  /** @ngInject */
  function config($logProvider, $httpProvider, adalAuthenticationServiceProvider) {
    
    //----------------------------------------------------------------------
    // ADAL configuration
    //----------------------------------------------------------------------
    adalAuthenticationServiceProvider.init({
      //tenant: 'a204560f-5ffc-4cf5-a723-22981d99c4f9',
      //clientId: '6bfd5aea-41ff-448a-96d3-6398844d8e75',
      clientId: '8678d1cb-82dc-4520-a750-71d1e4f38f57',
      postLogoutRedirectUri: 'http://localhost:3000',
      endpoints: {
          'https://graph.microsoft.com': 'https://graph.microsoft.com'
      }
    }, $httpProvider);
            
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();
